@import '../common/styles/sass/variables.scss';

.nav {
    border: 1px solid;
    width: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;

    & a {
        font-family: 'Poppins', sans-serif;
        color: $titleColor;
        font-size: 16px;
        font-weight: 500;
        padding: 20px 0;
        text-decoration: none;
        line-height: 1.71429;
        text-align: left;
    }

    & a:hover {
        color: $primeryColor;
    }

    .logo {
        height: 55px;
        width: 55px;
    }
}